import React, { Component } from "react";
import { Map, InfoWindow, Marker, GoogleApiWrapper } from "google-maps-react";

export class MapContainer extends Component {
  state = {
    showingInfoWindow: false,
    activeMarker: {},
    selectedPlace: null,
    markers: null
  }

  // componentDidMount() {
  //   if (!!this.state.selectedPlace) {
  //     this.setState((prevState, props) => {
  //       return {
  //         selectedPlace: props.selectedPlace,
  //         activeMarker: prevState.markers[props.selectedPlace.name],
  //         showingInfoWindow: true
  //       }
  //     })
  //   }
  // }

  handleMarkerClick = (props, marker, e) => {
    this.animateMarker(marker, props.map);
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });
  };

  handleMapClick = props => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      });
    }
  };

  animateMarker = (marker, map) => {
    map.setCenter(marker.position);
    marker.setAnimation(this.props.google.maps.Animation.BOUNCE);
    setTimeout(function () {
        marker.setAnimation(null);
    }, 2000);
  }

  fetchMarkers = (props, map) => {
    console.log(map.markers)
  }

  render() {
    const { places, google } = this.props;
    const { activeMarker, showingInfoWindow, selectedPlace } = this.state;
    const initialCenter = {
      lat: 39.90557676703715,
      lng: 116.40739630000007
    };
    const zoom = 13;

    return (
      <Map
        google={google}
        initialCenter={initialCenter}
        zoom={zoom}
        onClick={this.handleMapClick}
        onReady={this.fetchMarkers}
      >
        {places.map(place => (
          <Marker
            key={place.name}
            onClick={this.handleMarkerClick}
            name={place.name}
            position={place.location}
          />
        ))}

        <InfoWindow marker={activeMarker} visible={showingInfoWindow}>
          <h1>{selectedPlace ? selectedPlace.name : ""}</h1>
        </InfoWindow>
      </Map>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: "AIzaSyApmoY54_QVp80zMhBn6K9phNui_6SbpL0"
})(MapContainer);



